import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import {
  Form,
  Input,
  Radio,
  InputNumber,
  Button,
  Card,
  Layout,
  Menu,
  Divider,
} from "antd";

const { Header, Content, Footer } = Layout;

const url = "https://662d-27-55-93-160.ngrok.io/";
const postUrlCalculate = url + "water/area/calculate";

const menuItems = ["", "", "", "", "", "/"].map((key) => ({
  key,
  label: `${key}`,
}));
const tabList = [
  {
    key: " - ",
    tab: " - ",
  },
  {
    key: "",
    tab: "",
  },
  {
    key: "",
    tab: "",
  },
  {
    key: "",
    tab: "",
  },
];


const FormDemand = () => {

  const [area, setArea] = useState(null);
  const [numPeople, setNumPeople] = useState(null);
  const [waterDemand, setWaterDemand] = useState(null);
  const [result, setResult] = useState(null);
  const [postResponseFormulaCalculate, setPostResponseFormulaCalculate] = useState(null);

  const onChangeRadioArea = (e) => {
    console.log("radio checked", e.target.value);
    setArea(e.target.value);
  };

  const onChangeNumPeople = (e) => {
    console.log("num people changed", e.target.value);
    setNumPeople(e.target.value);
  };

  const onChangeWaterDemand = (e) => {
    console.log("water demand changed", e.target.value);
    setWaterDemand(e.target.value);
  };

  const onClickCalculate = async (e) => {
    await axios.post(postUrlCalculate, {
      "area_name": area,
      "number_person": numPeople,
      "number_day" : waterDemand
    })
      .then((response) => {
        setPostResponseFormulaCalculate(response.data)
        setResult(response.data.data.result)
      });
  };

  postResponseFormulaCalculate != null ? console.log("-", result) : console.log("-null-");

  return (
    <>
      <Header style={{ backgroundColor: "#1890FF" }}>
        <h1
          style={{
            float: "left",
            width: "20%",
            color: "white",
          }}>
          FARM DESIGN
        </h1>
        <Menu
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["0"]}
          items={menuItems}
          style={{
            float: "right",
            width: "50%",
            backgroundColor: "#1890FF"
          }}
        />
      </Header>
      <Content
        style={{
          padding: "2% 10%",
        }}
      >
        <h1 style={{ textAlign: "start" }}></h1>
        <Card
          tabList={tabList}
          bordered={true}
          headStyle={{}}
          bodyStyle={{ textAlign: "start", boxShadow: "2px 2px 2px 2px lightgray" }}
        >
          <Form
            labelCol={{
              span: 3,
            }}
            wrapperCol={{
              span: 24,
            }}
            layout="horizontal"
            labelAlign={"left"}
            style={{
              fontWeight: "bold",
            }}
          >
            <Form.Item label="">
              <Radio.Group onChange={onChangeRadioArea} value={area}>
                <Radio value="">  </Radio>
                <Radio value="">  </Radio>
                <Radio value="">  </Radio>
                <Radio value="">  </Radio>
                <Radio value="">  </Radio>
                <Radio value="">  </Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item label="" onChange={onChangeNumPeople}>
              <InputNumber style={{ width: "20%" }} /> 
            </Form.Item>
            <Form.Item label="" onChange={onChangeWaterDemand}>
              <Input style={{ width: "20%" }} /> 
            </Form.Item>
            <Divider />
            <Form.Item label="">
              <Input
                disabled style={{
                  width: "20%",
                  fontWeight: "bold",
                  color: "#1890FF",
                }}
                value={result}
              /> 
              <Button
                type="primary"
                onClick={onClickCalculate}
                style={{
                  float: "right",
                  width: "20%",
                  fontWeight: "bold"
                }}
              >
                
              </Button>
            </Form.Item>
          </Form>
        </Card>
      </Content>
      <Footer
        style={{
          textAlign: 'center',
        }}
      >
        Copyright 2022 Thai Water Farm. All rights reserved.
      </Footer>
    </>
  );
};

export default () => <FormDemand />;